@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-family: 'Lato', sans-serif;
}

a {
    text-decoration: none;
}

::placeholder {
    color: rgb(58, 58, 58);
}

input:focus, textarea:focus {
    &::placeholder {
        color: gray;
    }
}